.entry_first {
  background-color: rgba(255, 215, 0, 0.8);
}

.entry_second {
  background-color: rgb(192,192,192,0.8);;
}

.entry_third {
  background-color: rgb(205,127,50,0.7);
  
}

.entry_normal {
  opacity: 1;
}

@media (pointer: none), (pointer: coarse) {
  .button_row {
    display: flex;
  }
}

@media (pointer: fine) {
  .button_row {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}

.button {
  color: #fc4c02;
  background-color: #fc4c02;
  margin: 0.1em;
}

.ranktext_td {
  display: inline-block;
}

.ranktext_rank {
  font-weight: bold;
}

.ranktext_score {
  font-weight: normal;
  opacity: 0.7;
}

.header {
  color: #fc4c02;
  /* text-align: center; */
}

.th {
  text-align: center;
}

.score {
  opacity: 0.7;
}

.divider {
  border-top: solid 1px #1a1a1a;
  clear: both;
  height: 1px;
  width: 100%;
}

.entry_border {
  color: red;
  border: 50px solid green;
}

.headerHeadline {
  color: #fc4c02;
  display: flex;
  font-family: Elianto;
  font-size: 4rem;
  padding-top:2rem;
}

@media (pointer: fine) {
  .headerHeadline {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
}
